const axios = require("axios");
const { ServiceProduct } = require("./Product");

class HTTPFetcher {

    async fetchProducts(){
        const data = await axios.default.get("https://electricity-webserver.herokuapp.com/services");
        return data.data.products.map(element => {
            const product = new ServiceProduct();
            product.label = element.label;
            product.revision = element.revision;
            return product;
        });
    }
}

exports.HTTPFetcher = HTTPFetcher;