const { ProductFetcher } = require("./ProductFetcher");

class ProductMaker {
    /**
     * @type {ProductFetcher} fetcher
     */
    fetcher = null;
    
    /**
     * 
     * @param {ProductFetcher} fetcher 
     */
    constructor(fetcher){
        this.fetcher = fetcher;
    }

    async getProducts(){
        const products = await this.fetcher.fetchProducts();
        return products;
    }
}
exports.ProductMaker = ProductMaker;