const { ServiceProduct, Product } = require("./Product");


class ProductFetcher {
    constructor(...data){
    }
    async fetchProducts(){
        return [new Product()]
    }
}


class LocalProductFetcher extends ProductFetcher {

    

    async fetchProducts(){
        return [
            {
                label: "Cocinas/Lavadoras",
                revision: 5
            },{
                label: "TV",
                revision: 2
            },{
                label: "L.C.D./LCD",
                revision: 3
            },{
                label: "Equipos de Sonido",
                revision: 3
            },{
                label: "Micro Ondas",
                revision: 2
            },{
                label: "D.V.D./Blue Ray",
                revision: 2
            },{
                label: "Power",
                revision: 4
            },{
                label: "Licuadora Oster",
                revision: 2
            },{
                label: "Cornetas",
                revision: 2
            },
            {
                label: "Amplificadores",
                revision: 4
            },{
                label: "Consolas/DirecTV",
                revision: 2
            }
        ].map(element => {
            const serviceProduct = new ServiceProduct();
            serviceProduct.label = element.label;
            serviceProduct.revision = element.revision;
        })
    }
}

exports.ProductFetcher = ProductFetcher;
exports.LocalProductFetcher = LocalProductFetcher;